
  import {defineComponent} from 'vue';

  type DataSourceItem = { text: string, value: string }
  export default defineComponent({
    props: {
      type: String,
      classPrefix: String,
      dataSource: Array
    },
    methods:{
      select(item:DataSourceItem){
        this.$emit('update:type', item.value);
      }
    }
  });
