
  import {defineComponent} from 'vue';

  export default defineComponent({
    props:{
      fieldName: {
        type: String,
        required: true
      },
      placeholder:{
        type: String,
        default: ''
      },
      value:{
        type: String,
        default: ''
      }
    },
    methods:{
      changeValue(value: string){
        this.$emit('update:value', value);
      }
    },
  });
